@import url("https://cdn.jsdelivr.net/npm/bootstrap-icons@1.3.0/font/bootstrap-icons.css");

* {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.index-container {
  height: 100vh;
  width: 100vw;
}

nav {
  background-color: rgb(65, 65, 65);
  width: 100vw;
  height: 3rem;
  display: flex;
  flex-direction: row;
  align-items: center;
}

nav .title::before {
  content: "CLassroom | ";
}

nav * {
  color: white;
  margin-left: 0.5rem;
  margin-right: 0.5rem;
}

nav a {
  font-size: 15px;
  font-weight: 100;
  text-decoration: none;
  border-radius: 1rem;
}

.email-in-the-screen {
  background-color: rgb(145, 16, 16);
  color: white;
}

.email-in-the-screen:hover {
  background-color: rgb(145, 16, 16);
}

nav a:hover {
  background-color: rgb(37, 37, 37);
}

nav .title {
  font-size: 25px !important;
  font-weight: bold;
}

.index-container div {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: wrap;
}


.index-container div .text {
  color: white;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: center;
  padding: 5rem;
  padding-bottom: 30vh;
}

.index-container div .text p {
  display: inline;
}

.index-container div .text .title {
  font-size: 72px;
}

.index-container div .text .des {
  margin-top: 5rem;
  font-weight: 100;
  color: gray;
  font-size: 25px;
  width: 20rem;
}

img {
  width: auto;
  height: auto;
}

@media screen and (max-width: 500px) {
  .index-container div .text {
    padding: 1rem;
  }

  .index-container div .text .title {
    font-size: 50px;
  }

  nav .title::before {
    content: "";
  }
}

/* upload */

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.upload {
  font-size: 72px;
  color: white;
  margin: 5rem;
}

.control {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin: 2rem;
}

.control audio {
  flex: 1;
  margin: 2rem;
  height: 3rem;
  min-width: 20rem;
}

.control label {
  color: white;
  min-width: 20rem;
  font-size: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: gray;
  border-radius: 1rem;
  border: 1px solid white;
  height: 3rem;
  margin: 2rem;
  flex: 1;
}

.control input {
  display: none;
}

.img-upload {
  margin: 5rem;
  display: flex;
  flex-direction: column;
}

.img-upload input {
  display: none;
}

.img-upload label {
  flex: 1;
  color: white;
  font-size: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(49, 49, 49);
  border-radius: 1rem;
  border: 1px solid rgb(255, 255, 255);
  height: 3rem;
  margin: 2rem;
}

.img-upload img {
  flex: 1;
  border: 1px solid white;
  width: 100%;
  height: 100%;
  max-width: 30rem;
  max-height: 30rem;
  overflow: hidden;
}


.console {
  color: white;
  margin: 5rem;
}

.console p {
  font-size: 46px;
}

@media screen and (max-width: 500px) {
  .control {
    margin: 0.5rem;
  }
}

.text-input {
  width: 80vw;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.text-input label {
  color: white;
  font-size: 25px;
  margin-right: 1rem;
  margin-left: 1rem;
}

.text-input input {
  height: 2rem;
  outline: none;
  border: 1px solid black;
  border-radius: 2rem;
  padding: 2px;
  width: 80%;
  font-size: 25px;
}


.send-button {
  color: white;
  font-size: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(49, 49, 49);
  border-radius: 1rem;
  border: 1px solid rgb(255, 255, 255);
  height: 2rem;
  width: 10rem;
  margin: 2rem;
}

/* listen */

.listen-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: linear-gradient(to bottom, rgb(15, 110, 78) 0%, rgb(0, 0, 0) 100%);
}

.listen-control {
  margin: 10rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.listen-title {
  word-break: break-word;
  color: rgb(204, 193, 193);
  font-size: 46px;
  margin: 5rem;
  margin-bottom: 0;
}

.listen-img-container{
  width: 30rem;
  max-height: 30rem;
  overflow: hidden;
}

.listen-img {
  width: 30rem;
  border-radius: 0.5rem;
}

.bi-play-fill, .bi-pause-fill {
  color: white;
  font-size: 56px;
}

.listen-control audio {
  display: none;
}

.progress{
  background-color: black;
  width: 100%;
  height: 0.5rem;
}

.bar{
  background-color: white;
  height: 0.5rem;
  width: 0;
}

meter::-webkit-meter-optimum-value {
  background: rgb(255, 255, 255);
}

meter::-webkit-meter-bar {
  background: black;
}

@media screen and (max-width: 600px) {
  .listen-title {
    margin: 1rem;
    margin-top: 5rem;
  }
  .listen-control{
    width: 100%;
    margin: 0;
    margin-top: 5rem;
    margin-bottom: 5rem;
  }
  .listen-img{
    width: 90%;
  }
  .listen-img-container{
    width: 90%;
  }
  .progress{
    width: 90%;
  }
}

/* login */

.login-container {
  margin-left: 20%;
  margin-right: 20%;
}

.login-title {
  color: white;
  font-size: 72px;
  margin-left: 2rem;
  margin-right: 2rem;
}

.login-control {
  display: flex;
  flex-direction: column;
  margin: 3rem;
}

.login-control label {
  font-size: 25px;
  color: white;
}

.login-control input {
  height: 2rem;
  margin-bottom: 1rem;
  margin-top: 1rem;
  border-radius: 3px;
  border: 1px solid black;
  outline: none;
  font-size: 25px;
  padding-left: 2px;
}

.login-container button {
  background-color: rgb(90, 90, 90);
  height: 2rem;
  border: 1px solid black;
  border-radius: 4px;
  width: 5rem;
  margin-top: 2rem;
  color: white;
  font-size: 18px;
}

.login-container button:hover {
  background-color: rgb(66, 66, 66);
}

@media screen and (max-width: 900px) {
  .login-container {
    margin: 0;
  }
}

/* explore */

.explore-container {
  background: linear-gradient(to bottom, rgb(74, 94, 87) 0%, rgb(0, 0, 0) 100%);
  color: white;
}

.explore-container h1 {
  font-size: 72px;
  padding: 2rem;
}

.item-father-container {
  margin: 5rem;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.item-container {
  width: 40rem;
  display: flex;
  padding: 1rem;
  flex-direction: column;
  margin: 2rem;
}

.item-container:hover {
  background-color: rgb(24, 24, 24);
}

.item-container .item-title {
  font-size: 46px;
  font-weight: bold;
  word-break: break-word;
  margin-bottom: 2rem;
  margin-top: 2rem;
}

.item-img-container{
  width: 30rem;
  max-height: 30rem;
  overflow: hidden;
}

.item-container img {
  border-radius: 0.5rem;
  width: 30rem;
  
}

a {
  color: white;
  text-decoration: none;
}

@media screen and (max-width: 850px) {
  .item-container {
    width: 90%;
    margin: 0;
  }

  .item-container img {
    width: 100%;
  }

  .item-img-container{
    width: 100%;
  }

  .item-father-container {
    margin: 0;
  }
}

/* account */

.account-container{
  color: white;
}

.account-container h1{
  padding: 5rem;
}